*{
    margin: 0;
    padding: 0;
}

.add-button{
    background-color: green;
    color: white;
    padding: 5px;
    width: 100px;
    border: none;
    border-radius: 10px;
    margin: 10px;
}

.Remove-button{
    background-color: red;
    color: white;
    padding: 5px;
    width: 100px;
    border: none;
    border-radius: 10px;
    margin: 10px;
}

.Edit-button{
    background-color: blueviolet;
    color: white;
    padding: 5px;
    width: 100px;
    border: none;
    border-radius: 10px;
    margin: 10px;
}

.input-box{
    width: 300px;
    height: 35px;
    font-size: 15px;
    border: 2px solid blue;
    border-radius: 5px;
}

.main-div{
    text-align: center;
    display: flex;
    flex-direction: column;
    
}

ul li{
    list-style-type: none;
}

.action-area{
    
    border-radius: 10px;
    background-color: wheat;
    
      
}